.be-drop-down .drop-down-list{display: none; position: absolute; top: 100%; left:-1px; z-index: 228; box-sizing: content-box; }
.be-drop-down .drop-down-list li{display: block;}
.be-drop-down{
	position: relative; display: block; font-size:13px; color: #b4b7c1; background: #FFF; padding: 15px 80px 15px 50px; margin-bottom:10px; border: 1px solid #edeff2; cursor: pointer; -webkit-border-radius: 3px; -moz-border-radius: 3px; border-radius: 3px;
	&:hover{border: 1px solid #0d58c8;}
	.drop-down-list{border-left: 1px solid #0d58c8;border-right: 1px solid #0d58c8;}
	&.color-2{background:#f6f6f7;}
	&.color-2:hover{background:#fff;color:#b4b7c1;}
	&.color-2 .drop-down-list{border-left: 1px solid #f6f6f7;border-right: 1px solid #f6f6f7;}
	&.color-2 .drop-down-list a{background:#f6f6f7;color:#b4b7c1;}
	&.color-2 .drop-down-list a:hover{background:#fff;color:#b4b7c1;}
	&.color-2.be-drop-down:hover i{color:#b4b7c1;}
	&.color-2.be-dropdown-active:hover{background:#f6f6f7;}
	&.color-2.be-dropdown-active i{color:#b4b7c1;}
	&.color-2.be-dropdown-active:after{color:#b4b7c1;}
	&.color-3{background:#383b43;color:#fff;border: 1px solid #383b43;}
	&.color-3:hover{background: #0d58c8;color: #FFF;border: 1px solid #0d58c8;}
	&.color-3.be-dropdown-active:hover{background:#383b43;border: 1px solid #383b43;}
	&.color-3 .drop-down-list{background:#383b43;border-left: 1px solid #383b43;border-right: 1px solid #383b43;}
	&.color-3 .drop-down-list a{color:#b4b7c1;}
	&.color-3 .drop-down-list a:hover{color:#fff;}
	&.color-3 .fa{color:#b4b7c1;}
	&.color-4{background:#0d58c8;color:#fff;  border: 1px solid #0d58c8;}
	&.color-4 .fa{color:#869edc;}
	&.color-4 .drop-down-list{background:#383b43;border-left: 1px solid #383b43;border-right: 1px solid #383b43;}
	&.color-4 .drop-down-list a{color:#b4b7c1;}
	&.color-4 .drop-down-list a:hover{color:#fff;}
	&.size-2{font-size:10px; padding: 18px 50px 17px 32px;}
	&.icon-none{padding: 14px 80px 14px 18px;}
}
.be-drop-down>i{position:absolute;top:50%;font-size: 18px; left: 20px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);-ms-transform:translateY(-50%);transform: translateY(-50%);}
.be-drop-down:hover, .be-drop-down.be-dropdown-active{background: #0d58c8; color: #FFF; }
.be-drop-down.be-dropdown-active:after{webkit-transform:translateY(-50%) rotate(180deg); -moz-transform:translateY(-50%) rotate(180deg); -ms-transform:translateY(-50%) rotate(180deg); transform: translateY(-50%) rotate(180deg); }
.drop-down-list{background: #0d58c8; color: #FFF; width: 100%; left: 0; }
