.drop-down-list a:hover{color: #FFF;background: #0d50b5;}
.drop-down-list,.be-drop-down:hover:after,.be-drop-down:hover i,.be-drop-down.be-dropdown-active:after, .be-drop-down.be-dropdown-active i{color: rgba(255,255,255,0.8)}
.be-drop-down:after{content:"\f107";font-family:FontAwesome;font-size:18px;position:absolute;top:50%;right:25px; webkit-transform:translateY(-50%) rotate(0);-moz-transform:translateY(-50%) rotate(0);-ms-transform:translateY(-50%) rotate(0);transform: translateY(-50%) rotate(0);}
