/*inputs*/
.form-group{
	position:relative;margin-bottom:30px;
	.form-input{font-size:13px; line-height:50px; font-weight:400; color:#b4b7c1; width:100%; height:50px; padding-left:20px; padding-right:20px; border:1px solid #edeff2; -webkit-border-radius:3px; -moz-border-radius:3px; border-radius:3px;
	}
	textarea.form-input{height:150px;}
	.form-group-icon{position:absolute; top:50%; right:20px; color:#b4b7c1; -webkit-transform: translateY(-50%); -moz-transform: ranslateY(-50%);-ms-transform:translateY(-50%);-o-transform:translateY(-50%);transform:translateY(-50%);}

	&.fg_icon .form-input{padding-right:40px;}
	&.fl_icon .form-input{padding-left:70px;}
	&.fl_icon .icon{position:absolute;top:1px;left:1px;width:48px;height:48px;background:#f6f6f7;color:#b5b8c2;text-align:center;line-height:50px;-webkit-border-top-left-radius: 2px; -webkit-border-bottom-left-radius: 2px; -moz-border-radius-topleft: 2px; -moz-border-radius-bottomleft: 2px; border-top-left-radius: 2px; border-bottom-left-radius: 2px;}
	&.color-2 .form-input{background:#f6f6f7;}
	&.color-3 .form-input{background:#383b43;color:#fff;}
	&.color-4 .form-input{background:#0d58c8;color:#fff;}
