}
#content-block .input-col .form-group{margin-bottom:30px;}
#content-block .input-col:last-child .form-group{margin-bottom:0px;}
.form-label{font-size:13px;line-height:18px;font-weight:400;color:#383b43;margin-bottom:15px;}

#content-block .form-block .form-input{margin-bottom:20px;}
/*ckeckbox*/
.form-checkbox{
	position:relative;margin-bottom: 15px;
	> input{position:absolute; width:20px;height:20px;z-index:2;opacity: 0;}
	> input + .check {position:relative; top:5px; display:inline-block; width:20px; height:20px; border:1px solid #edeff2; color:#0d58c8; cursor:pointer; z-index: -1;}
	> input + .check .fa{position:absolute;top:2px;left:2px; display:none;opacity:0;}
	> input:checked + span .fa{display:block; opacity:1;}
	&.style-2 > input + .check{-webkit-border-radius: 20px; -moz-border-radius: 20px; border-radius: 20px;}
	&.style-2 > input + .check .fa{display:none;}
	&.style-2 > input:checked + .check:before{content:'';display:block;width:8px;height:8px;position:absolute;top:50%;left:50%; margin-top:-4px; margin-left:-4px; background:#4a82d6;-webkit-border-radius: 8px; -moz-border-radius: 8px; border-radius: 8px;}
}
/*alerts*/
.alert{
	position:relative; font-size:13px; line-height:22px; color:#7a8192; padding:21px 21px 21px 60px;; background:#f6f6f7; border:1px solid #edeff2; margin-bottom:20px; -webkit-border-radius: 3px;-moz-border-radius: 3px; border-radius: 3px;
	.close{position:absolute;top:21px;left:21px;width:24px; height:24px; font-size: 11px; background:#0d58c8; color:#fff;opacity:1;-webkit-border-radius: 3px;-moz-border-radius: 3px;border-radius: 3px;}
	.alert-title{font-size:14px;line-height:18px;font-family: 'Conv_helveticaneuecyr-bold';color:#383b43;margin-bottom: 10px;}
	&.style-2{background:#0d58c8;color:#8ba1dd;}
	&.style-2 .close{background:#fff;color:#5b5e64;}
	&.style-2 .alert-title{color:#fff;}
	&.style-3{background:#383b43;color:#b4b7c1;}
	&.style-3 .alert-title{color:#fff;}
}
/*accordion*/
.accordion.style-1{
	border:1px solid #edeff2;-webkit-border-radius: 2px; -moz-border-radius: 2px; border-radius: 2px;
	.acc-title{position:relative;font-size:13px;line-height:18px;color:#b4b7c1;background:#f6f6f7;padding: 11px 45px 11px 15px;cursor:pointer;border-bottom:1px solid #edeff2;}
	.acc-panel:last-child .acc-title{border-bottom:0px;}
	.acc-title.active{background:#0d58c8;color:#fff;}
	.acc-title:after{content:"\f078";display:inline-block;position:absolute;top:0px;right:15px;font: normal normal normal 14px/1 FontAwesome;font-size: 14px;line-height:37px;color:transparent;text-rendering: auto;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;transform: translate(0, 0)}
	.acc-title.active:after{color:#8da4e0;}
	.acc-body{display:none;font-size:13px;line-height:18px;padding:27px 40px 25px 40px;}
	.acc-body.active{display:block;}
	.acc-body p{margin-bottom:15px;}
	.acc-body a{color:#b4b7c1;}
	.acc-body a:hover{color:#0d58c8;}
}
.accordion.style-2{
	.acc-panel{margin-bottom:10px;border:1px solid #edeff2;}
	.acc-panel:last-child .acc-title{border-bottom:0px;}
	.acc-title{position:relative;font-size:14px;line-height:21px;color:#383b43;background:#fff;padding: 14px 45px 14px 70px;cursor:pointer;}
	.acc-icon{position:absolute;top:0px;left:0px;bottom:0px;width:50px;background:#f6f6f7;}
	.acc-icon:before{content: "\f067";position:absolute;top:50%;left:50%;display:block;width:14px;height:14px;margin-top:-7px;margin-left:-7px;color:#b4b7c1;text-align:center; font: normal normal normal 14px/1 FontAwesome;font-size: inherit; text-rendering: auto; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; transform: translate(0, 0);}
	.acc-title.active{background:#f6f6f7;border-bottom:1px solid #edeff2;}
	.acc-title.active .acc-icon{background:#0d58c8;}
	.acc-title.active .acc-icon:before{content: "\f068";color:#8da4e0;}
	.acc-body{display:none;background:#f6f6f7; color:#7a8192; font-size:13px;line-height:22px;padding:25px 30px 25px 30px;}
	.acc-body.active{display:block;}
	.acc-body p{margin-bottom:20px;}
}
